<template>
    <div class="sub">
        <el-button type="primary" @click="updateOption">更新订阅</el-button>
    </div>
</template>

<script>
import { updateBub } from "../api";
export default {
    data() {
        return {};
    },
    methods: {
        updateOption() {
            updateBub().then((res) => {
                this.$message.info(res.message);
            });
        },
    },
};
</script>

